'use client';

import { ChangeEvent, useEffect, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";

import {
  TableHead,
  TableRow,
  TableCell,
  TableHeader,
  TableBody,
  Table
} from '@/components/ui/table';
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle
} from '@/components/ui/card';

import { ChevronLeft, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { InvoiceRow } from './invoice';
import { showToast } from '@/lib/utils/toast-helper';

import { ToastContainer, toast } from 'react-toastify';

import { Invoice } from '@/lib/models/invoices';

import { useIndexedDB } from "@/lib/hooks/useIndexedDB";
import { DBConfig } from "@/lib/data/dbconfig";

import { ParseAllObjects } from "@/lib/utils/utils"

export function InvoicesTable({
  currentPageNo,
  offset,
  totalItems
}: {
  currentPageNo: number,
  offset: number;
  totalItems: number;
}) {

  const [invoices, setInvoices] = useState<Invoice[] | null>([]);

  const router = useRouter();
  const searchParams = useSearchParams();

  let itemPerPage = 10;

  const {
    putValue,
    getValue,
    getAllValue,
    updateValue,
    deleteValue,
    isDBConnecting,
  } = useIndexedDB(DBConfig.name, [DBConfig.invoiceTable]);

  let invoiceList: any;

  const getAllInvoices = () => {
    getAllValue(DBConfig.invoiceTable).then((value) => {
      setInvoices(ParseAllObjects<Invoice>(value));
    });
  };

  // Effect to fetch data when database connection status changes
  useEffect(() => {
    if (!isDBConnecting) {
      getAllInvoices();
      
      if (searchParams.get("status")) {
        showToast("success", "Data has been saved!");
      }
    }
    
  }, [isDBConnecting]);

  function prevPage() {
    router.back();
  }

  async function nextPage() {
    
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Invoices</CardTitle>
        <CardDescription>
          Invoice Listing
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Item</TableHead>
              <TableHead>Vendor</TableHead>
              <TableHead className="hidden md:table-cell">Place</TableHead>
              <TableHead className="hidden md:table-cell">Date</TableHead>
              <TableHead className="hidden md:table-cell">Type</TableHead>
              <TableHead className="hidden md:table-cell">Amount</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {invoices?.length ? (
              invoices?.map((invoice: any) => (
              <InvoiceRow key={invoice.id} invoice={invoice} />
            ))
            ) : (
              <TableRow>
                <TableCell className="font-medium"><span>No record found</span></TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </CardContent>
      <CardFooter>
        
      </CardFooter>
    </Card>
  );
}
